<template>
    <div class="container flex-grow-1 container-p-y">
        <h4 class="fw-bold py-3 mb-4">
            <span class="text-muted fw-light text-center">SAIV{{store.state.USUARIO.oficina}} /</span> Cámara Gesell / Atención de Menores de Edad
        </h4>
         <!-- Bordered Table -->
        <div class="card">
            <div class="card-body">                
                <TableVue ref="tablaCamaraGessellAtencionMenoresRef" 
                    :columnas="columnas" 
                    :url="urlTabla" 
                    @handleChangedLoading="handleChangedLoading"
                    @handleChangedData="handleChangedData"
                >
                    <template v-slot:col>
                        <div class="col-sm-12 col-md-12 m-1 col-lg-3">
                            <div class="input-group border-primary flex justify-content-end">
                                <span class="input-group-text border-primary bg-label-primary text-white text-center border-white" for="tipo-caso">
                                    Tipo Caso<i class=' bx bxs-label' ></i>
                                </span>
                                <select class="form-select bg-label-primary text-white border border-primary" v-model="tipoCaso" 
                                    @change="tablaCamaraGessellAtencionMenoresRef.handleChangeFiltro(tipoCaso)" >                                     
                                    <template v-for="item in ['Todos', 'Denuncia', 'Sin Denuncia']" :key="item" >
                                        <option class=" bg-label-primary text-white fw-semibold" :value="item">{{item}}</option>
                                    </template>
                                </select>
                            </div>   
                        </div>
                    </template>
                    <template v-slot:button >
                        <button class="btn btn-primary m-1 justify-items-start" 
                            @click="handleClickNuevo" :disabled="loading">
                            <i class="bx bx-plus" style="font-size:20px"/> Nuevo
                        </button> 
                    </template> 
                    <template v-slot:tbody>
                    </template>
                </TableVue> 
            </div>
        </div>
        <ModalVue ref="modalCamaraGessellAtencionMenoresRef" @handleActualizarDatosTabla="handleActualizarDatos">
            <template v-slot:cardBody>
                <Form :showVer="(showVer ?? false)" ref="formCamaraGessellAtencionMenoresRef"/>
            </template>
        </ModalVue>
    </div>
</template>
<script>
// Servicios
import store                from '@/store/index'
import servicio             from '@/services/crud'
import servicios_otros      from '@/services/otros'

import ModalVue             from '@/components/Modal.vue'
import TableVue             from '@/components/layout/Table.vue'
import Form                 from '@/components/camaraGessell/AtencionMenoresEdad.vue'
import AccionesTable        from '@/components/layout/AccionesTable.vue'
import AdvertenciaVue       from '@/components/layout/Advertencia.vue'
const columnas = [
    {
        nombre:"Código", 
        class:"text-center bg-primary text-white fw-bold",    
        key:"",   
        style:"", 
    },
];
const urlTabla  = 'saiv/camara/gessell/programacion/index';

import { defineComponent, ref } from 'vue'
export default defineComponent({
    components:{
        Form,
        ModalVue,
        TableVue,
        AccionesTable,
        AdvertenciaVue,
    },
    setup(props) {
        //Variables
        const loading           = ref(false);
        const showVer           = ref(false);
        const data              = ref([]);
        const tipoCaso          = ref('Todos');
        const codigoCaso        = ref(null);
        const intercambiarForm  = ref(true);

        // Ref
        const tablaCamaraGessellAtencionMenoresRef  = ref(null);
        const modalCamaraGessellAtencionMenoresRef  = ref(null);
        const formCamaraGessellAtencionMenoresRef   = ref(null);
        const formCasoRef                           = ref(null);

        // Metodos
        const handleChangedLoading = _loading => {
           // loading.value = _loading;
        };

        const handleChangedData = async (_data) => {
            data.value = _data
        };

        const handleActualizarDatos = () => {
            loading.value = true;
            formCamaraGessellAtencionMenoresRef.value.resetForm();
            tablaCamaraGessellAtencionMenoresRef.value.handleActualizarDatos();
            loading.value = false;
        };

        const handleClickNuevo = () => {
            showVer.value = null;
            formCamaraGessellAtencionMenoresRef.value.resetForm();
            modalCamaraGessellAtencionMenoresRef.value.showModal();
        };

        return {
            // Const
            urlTabla, 
            columnas,
            store,

            // Variables
            intercambiarForm,
            codigoCaso,
            tipoCaso,
            loading,
            showVer,
            data,

            // Ref
            tablaCamaraGessellAtencionMenoresRef,
            modalCamaraGessellAtencionMenoresRef,
            formCamaraGessellAtencionMenoresRef,
            formCasoRef,

            // Metodos
            handleActualizarDatos,
            handleChangedLoading,
            handleChangedData,
            handleClickNuevo,
        }
    },
});
</script>